.vanilla-tree-viewer__tree{box-sizing:border-box;min-width:200px;height:200px;margin:0;padding:20px 0;overflow-x:scroll;white-space:nowrap;background-color:#EBEDEF}@media screen and (min-width: 480px){.vanilla-tree-viewer__tree{width:200px;height:auto}}.vanilla-tree-viewer__tree-node{display:flex;flex-direction:row;flex-wrap:nowrap;align-items:stretch;justify-content:flex-start;height:18px;padding:6px 0;cursor:pointer}.vanilla-tree-viewer__tree-node:hover{background-color:#DBDDE0}.vanilla-tree-viewer__tree-node span{line-height:18px}.vanilla-tree-viewer__tree-node svg{margin-right:7px}
